<template>
	<el-card>
		<el-form
			status-icon
			:model="ruleForm"
			:rules="rules"
			ref="ruleForm"
			label-width="100px"
			class="demo-ruleForm"
		>
			<el-form-item label="" prop="name">
				<el-input v-model="ruleForm.name"></el-input>
			</el-form-item>
			<el-form-item label="" required>
				<el-select v-model="ruleForm.permission" placeholder="">
					<el-option label="(NORMAL)" value="normal" v-if="userPermi==='admin'|| userPermi==='root'"></el-option>
					<el-option label="(ADMINISTRATOR)" value="admin" v-if="userPermi==='root'"></el-option>
					<el-option label="(ROOT)" value="root" v-if="userPermi==='root'"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="" prop="pass" required>
				<el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
			</el-form-item>
			<el-form-item label="" prop="checkPass" required>
				<el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
			</el-form-item>
			<el-form-item
				prop="email"
				label=""
				:rules="[
      { type: 'email', message: '', trigger: ['blur', 'change'] }
    ]"
			>
				<el-input v-model="ruleForm.email"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="11">
					<el-form-item prop="birth">
						<el-date-picker type="date" placeholder="" v-model="ruleForm.birth" style="width: 100%;"></el-date-picker>
					</el-form-item>
				</el-col>
			</el-form-item>
			<el-form-item label="" prop="desc">
				<el-input type="textarea" v-model="ruleForm.desc"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
				<el-button @click="resetForm('ruleForm')"></el-button>
			</el-form-item>
		</el-form>
	</el-card>
</template>

<script>
	export default {
		data() {
			var checkName = (rule, value, callback) => {
				if (!value) {
					return callback(new Error(""));
				}
				let url = "/verify/checkUserName";
				this.$axios
					.post(url, {
						username: this.ruleForm.name
					})
					.then(({ data }) => {
						if (!data) {
							return callback(new Error(""));
						}
						callback();
					});
			};
			var validatePass = (rule, value, callback) => {
				if (value === "") {
					callback(new Error(""));
				} else {
					if (this.ruleForm.checkPass !== "") {
						this.$refs.ruleForm.validateField("checkPass");
					}
					callback();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if (value === "") {
					callback(new Error(""));
				} else if (value !== this.ruleForm.pass) {
					callback(new Error("!"));
				} else {
					callback();
				}
			};
			return {
				ruleForm: {
					email: "",
					permission: "",
					birth: "",
					name: "",
					pass: "",
					checkPass: "",
					desc: ""
				},
				rules: {
					name: [
						{ required: true, message: "", trigger: "change" },
						{
							min: 6,
							max: 13,
							message: " 6  13 ",
							trigger: "change"
						},
						{ validator: checkName, trigger: "change" }
					],
					pass: [
						{ validator: validatePass, trigger: "blur" },
						{ min: 6, max: 13, message: " 6  13 ", trigger: "blur" }
					],
					checkPass: [{ validator: validatePass2, trigger: "blur" }]
				}
			};
		},
		computed: {
			userPermi() {
				return this.$store.state.userInfo.permissions;
			}
		},
		methods: {
			submitForm(formName) {
				let that = this;
				this.$refs[formName].validate(valid => {
					if (valid) {
						that.requestTo();
					} else {
						console.log("error submit!!");
						return false;
					}
				});
			},
			requestTo() {
				let that = this;
				let url = "/backend/regg";
				this.$axios
					.post(url, {
						reg_name: this.ruleForm.name,
						reg_pwd: this.ruleForm.pass,
						reg_birth: this.ruleForm.birth,
						reg_email: this.ruleForm.email,
						reg_desc: this.ruleForm.desc,
						reg_permissions: this.ruleForm.permission
					})
					.then(({ data }) => {
						if (data.status !== 1) {
							this.$message({
								type: "error",
								message: ","
							});
							return;
						}
						this.$message({
							type: "success",
							message: ""
						});
						// window.reload();
						this.$router.push({
							path: "/blank"
						});
						// that.resetFormData();
						console.log(data);
					});
			},
			resetFormData() {
				let obj = this.ruleForm;
				for (let val in obj) {
					obj[val] = "";
				}
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		}
	};
</script>

<style lang="scss">
	.hr-line {
		margin-top: 50px 0;
	}
</style>
